import github from './github.png';
import resume from './resume.png';
import resume_pdf from './Resume_2023.pdf'
import './App.css';
import Blobity from 'blobity';

const options = { color: 'rgb(180, 180, 180)' };
const blobity = new Blobity(options);

function App() {
  return (
    <div className="App">
      <h1>Hi there, this is Alex Yan</h1>
      <h2>B.S. Computer Science @<a id={"nyu"} href={"http://www.nyu.edu"}>NYU</a></h2>
      <h2>Current research intern @<a href={"https://ai4ce.github.io/"}>NYU ai4ce Lab</a></h2>
      <h2>Seeking SDE interns for 2023 summer</h2>
      <a href={"https://github.com/Alex-YuYan"}><img data-blobity src={github} width={"60px"} height={"60px"}/></a>
      <a href={resume_pdf}><img data-blobity src={resume} width={"60px"} height={"60px"}/></a>
    </div>
  );
}

export default App;
